<template>
	<view class="chartContainer">
		<canvas id="chartContainer" ref="chartContainer"></canvas>
	</view>
</template>

<script setup>
	import * as echarts from 'echarts'
	import {
		getCurrentInstance,
		onMounted,
		ref
	} from 'vue'
	onMounted(() => {
		initChart()
	})
	const chartContainer = ref()
	const initChart = () => {
		const myChart = echarts.init(chartContainer.value)

		myChart.setOption({
			title: {
				text: 'ECharts 入门示例'
			},
			tooltip: {},
			xAxis: {
				data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
			},
			yAxis: {},
			series: [{
				name: '销量',
				type: 'bar',
				data: [5, 20, 36, 10, 10, 20]
			}]
		});
	}
</script>

<style scoped lang="scss">
	.chartContainer {
		width: 100%;
		height: 100%;
	}
</style>